import React from "react";
import styles from "./ContactMe.module.css";
import TextHeading from "../Components/Heading/TextHeading";
import Mail from "@mui/icons-material/Mail";
import Twitter from "@mui/icons-material/Twitter";
import GitHub from "@mui/icons-material/GitHub";
import LinkedIn from "@mui/icons-material/LinkedIn";
import MobileFriendlyTwoTone from "@mui/icons-material/MobileFriendlyTwoTone";
const ContactMe = () => {
  return (
    <section id="contact">
      <TextHeading cont="Contact Me" />

      <div className={styles.project}>
        <div className={styles.left}>
          <h1 className={styles.heading_2}>Get In Touch</h1>
          <p className={styles.desc}>
            Feel free to get in touch with me. I am always open to discussing
            new projects, creative ideas or opportunities to be part of your
            visions.
          </p>
          <div className={styles.contacts}>
            <a href="mailto:sunilrath.ssr@gmail.com" className={styles.contact}>
              <Mail />
              <span>sunilrath.ssr@gmail.com</span>
            </a>
            <br />
            <a href="tel:+918947816217" className={styles.contact}>
              <MobileFriendlyTwoTone />
              <span>8947816217</span>
            </a>
            <br />
            <a
              href="https://www.linkedin.com/in/sunil-singh-rathore-5834b4239/"
              target="_blank"
              rel="noopener noreferrer"
              className={`${styles.contact} ${styles.block}`}
            >
              <LinkedIn />
              <span>sunil-singh-rathore-5834b4239</span>
            </a>
            <br />
            <a
              href="https://github.com/Sunilrath101"
              target="_blank"
              rel="noopener noreferrer"
              className={`${styles.contact} ${styles.block}`}
            >
              <GitHub />
              <span>sunilrath101</span>
            </a>
            <br />
            <a
              href="https://twitter.com/SunilRath101"
              target="_blank"
              rel="noopener noreferrer"
              className={`${styles.contact} ${styles.block}`}
            >
              <Twitter />
              <span>SunilRath101</span>
            </a>
          </div>
        </div>
        <div className={styles.right}>
          <form action="" className={styles.form}>
            <input type="text" placeholder={"Your Name"} required />
            <input type="email" placeholder={"Email"} required />
            <input type="text" placeholder={"Subject"} required />
            <textarea
              rows="4"
              type="text"
              placeholder={"Write your message here"}
              required
            />

            <button className={styles.btn}>Send message</button>
          </form>
        </div>
      </div>
    </section>
  );
};

export default ContactMe;
